import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {DashboardComponent} from './dashboard/dashboard.component';


const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Admin'
    },
    children: [
      {
        path: '',
        redirectTo: 'admin'
      },
      {
        path: 'admin',
        component: DashboardComponent,
        data: {
          title: 'Admin'
        }
      },
      {
        path: 'system-config',
        loadChildren: () => import('./SystemConfig/system-config/system-config.module').then(m => m.SystemConfigModule)
      },
      {
        path: 'email-config',
        loadChildren: () => import('./EmailConfig/email-config/email-config.module').then(m => m.EmailConfigModule)
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
